html {
  font-family: Roboto, Arial, sans-serif;
  font-size: 10px;
  color: #060b35;
  line-height: 14px;
}

body {
  margin: 0;
}

body,
header,
nav,
a,
ul,
li {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body,
html {
  font-family: Roboto, Arial, Helvetica, sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  height: 100%;
}
